.share-social {
	display: none;
	position: absolute;
	bottom: -10px;
	padding: 34px;
	padding-bottom: 37px;
	width: 100%;
	color: $text-color;
	background-color: $white;
	transform: translate(0, 100%);
	box-shadow: 4px 4px 14px rgba(0, 0, 0, 0.2);
	box-sizing: border-box;
	opacity: 1;
	
	z-index: -1;
	/*transition: all 0.3s ease 0s;*/
}

.share-social--shown {
	display: block;
	z-index: 20;
}

.share-social__title {
	margin: 0;
	margin-bottom: 19px;
	font-size: 21px;
	font-weight: 700;
}

.share-social .share-social__list {
	@include list-style;
	width: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
}

.share-social .share-social__item {
	margin: 0;
	margin-right: 20px;
	margin-bottom: 20px;
	width: 37px;
	height: 37px;
	background-color: #676767;
	border-radius: 7px;

	&:last-child {
		margin-right: 0;
	}
}

.share-social .share-social__link {
	position: relative;
	@include link-style;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 37px;
	height: 37px;
}

.share-social__link svg {
	width: 21px;
	height: 21px;
	fill: $white;
}

.share-social__link span {
	position: absolute;
	bottom: 3px;
	right: 40%;
	display: block;
	padding: 0;
	height: 0;
	width: 0;
	font-size: 1px;
	font-weight: 700;
	white-space: nowrap;
	border-radius: 3px;
	transform: translate(0, 100%);
	opacity: 0;
	z-index: -1;
	transition: all 0.3s ease 0s;
}

.share-social__link:hover span {
	padding: 8px 12px;
	height: auto;
	width: auto;
	font-size: 12px;
	background-color: $white;
	border: 1px solid #a4a4a4;
	z-index: 25;
	opacity: 1;

}

.share-social__form p {
	margin: 0;
	margin-bottom: 22px;
	font-size: 17px;
	font-weight: 600;
}

.share-social__form-wrap {
	display: flex;
	flex-direction: column;
}

.share-social__form-input {
	margin-bottom: 20px;
	padding: 7px 14px 8px;
	width: 100%;
	height: 34px;
	background-color: #ECEBEB;
	border-radius: 3px;
	box-sizing: border-box;
}

.share-social__form-wrap button {
	padding: 12px 25px;
	color: $white;
	font-size: 14px;
	font-weight: 600;
	background-color: $light-green;
	border-radius: 3px;
}

@media (min-width: 768px) {

	.share-social {
		bottom: -16px;
		right: 0;
		width: 461px;
	}

	.share-social .share-social__list {
		flex-wrap: nowrap;
		margin-bottom: 23px;
	}

	.share-social .share-social__item {
		margin-bottom: 0;
	}

	.share-social__form-wrap {
		flex-direction: row;
		flex-wrap: nowrap;
		align-items: center;
	}

	.share-social__form-input {
		margin-right: 20px;
		margin-bottom: 0;
		width: 246px;
	}
}

@media (min-width: 1024px) {

	.share-social {
		transform: translate(80%, 100%);
	}
}

@media (min-width: 1440px) {

	.share-social {
		transform: translate(83%, 100%);
	}

}
